footer{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 50vh;
    background-color: #043E54;
  }
  
  .social{
    display: flex;
    border-bottom: 1px solid #eee;
    padding-bottom: 10px;
  
  }
  
  .social > a{
    cursor: pointer;
  }

  .social > a > img{
    height: 80px;
  }
  
  .copy-container{
    display: flex;
    margin-top: 20px;
    color: #eee;
    font-size: 18px;
    
    
  
  }
  
  
  .copy-container > p{
    margin: 0;
    
  }
  