.icon-section-landing {
    display: flex;
    flex-direction: column;
    justify-content: center;
    text-align: center;
    align-items: center;
    min-height: 75%;
    margin-top: 20px;
    margin-bottom: 80px;
  }
  
  .icon-section-landing > h2 {
    font-size: 45px;
    color: #043E54;
  }
  
  .icon-section-landing > .icon-container {
    display: grid;
    width: auto;
    grid-template-columns: 1fr 1fr 1fr;
    grid-template-rows: 1fr 1fr;
    column-gap: 20px;
    row-gap: 20px;
  }