.reviews{
    background-image: url("../assets/henry-be-228194-unsplash.jpg");
    background-position: center bottom;
    background-repeat: no-repeat;
    background-size: cover;
    
    height: 100vh;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    padding-left: 130px;
  }
  
  
  .review-cont{
    display: flex;
    justify-content: flex-start;
    align-items: center;
    background-color: rgba(255, 255, 255, 0.5);
    width: 450px;
    border-radius: 80px;
    padding: 20px;
    margin-bottom: 20px;
  }
  
  .review-cont > img{
    max-width: 80px;
    border-radius: 90px;
    margin-right: 20px;
  }
  
  .review-cont > .text {
    border-left: 1px solid #f49231;
    padding-left: 20px;
    max-height: 100px;
    min-height: 90px;
    
  }
  
  .review-cont > .text > h3{
    font-size: 18px;
    margin-bottom: 0px;
    color: #043E54;
  }
  
  .review-cont > .text > p{
    font-size: 15px;
    font-style: italic;
    margin-top: 0px;
    color: #043E54;
  }