.text-section{
    display: flex;
    align-items: center;
    justify-content: center;
    min-height: 50vh;
    background-color: #043E54;
    
}

.text-section > div{
    max-width: 60vw;

}

.text-section > div > p{
    color: white;
    font-size: 30px;
    font-weight: 600;
    text-align: center;
    margin: 10px;
  }
  
  
  .text-section > div > p > span{
    color: #f49231;
  }