.ReactModal__Content {
  width: 40%;
  min-height: 60%;
  /* background-image: url("../assets/Foto\ pag\ filtro\ blanca.png"); */
  background-color: rgba(0, 0, 0, 0.4);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  border: 2px solid  #043e54;
  display: flex;
  flex-direction: column;
  position: absolute;
  left: 30%;
  top: 20%;
  border-radius: 5px;
}

.contactForm {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.contactForm > button {
    background-color: rgba(0, 0, 0, 0.0);
    color: #f49231;
  font-size: 28px;
  border: 0;
  align-self: flex-end;
  margin: 3px;
  cursor: pointer;
}

.contactForm > p {
  color: #043e54;
  font-size: 28px;
  font-weight: 900;
  margin-top: 0;
}

.contactForm > form {
  display: flex;
  flex-direction: column;
  margin: 0 !important;
  width: 400px;
}

.contactForm > form > input {
  margin: 5px 0;
  padding: 6px;
  border: none;
  border-radius: 5px;
  height: 25px;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  background-color: rgba(255, 255, 255, 0.5);
}

.contactForm > form > input::placeholder {
  font-family: "Montserrat", sans-serif;
  font-size: 13px;
  font-style: italic;
}

.contactForm > form > textarea {
  padding: 6px;
  margin: 5px 0;
  border: none;
  border-radius: 5px;
  resize: none;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  background-color: rgba(255, 255, 255, 0.5);
}

.contactForm > form > textarea::placeholder {
  font-family: "Montserrat", sans-serif;
  font-size: 13px;
  font-style: italic;
}

.contactForm > form > #send {
  background-color: #043e54;
  border: 0;
  height: 45px;
  width: 230px;
  color: #f49231;
  margin-top: 20px;
  margin-bottom: 20px;
  font-size: 17px;
  border-radius: 40px;
  transition: all 0.2s ease-in-out;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  letter-spacing: 1px;
  cursor: pointer;
  align-self: center;
}
