.header{
  display: flex;
  justify-content: space-between;
  z-index: 10;
}

.header > img{
  height: auto;
  width: 200px;
  
}
  
nav {
  display: flex;

  align-items: center;
}

.menu {
  display: flex;
  justify-content: space-around;
  align-items: center;
}

.menu li {
  margin-left: 5px;
  font-size: 14px;
  font-weight: bold;
  text-decoration: none;
  color: white;
  display: block;
  border-right: 2px solid #f49231;
  padding-right: 5px;
}

.menu > li:last-child {
  border-right: 0px;
}

.menu li a{
  color: white;
  text-decoration: none;
}

.menu > li > a:hover {
  color: #f49231;
}

.menu li button {
  color: white;
  text-decoration: none;
  border: 0;
  background-color: transparent;
  font-family: "Montserrat", sans-serif;
  cursor: pointer;
  padding: 0;
}

.menu > li button:hover {
  color: #f49231;
}
