.hero-text-nosotros {
    position: absolute;
    top: 35%;
    left: 55%;
    width: 60%;
  }
  
  .hero-text-nosotros.porque{
    top: 20% !important;
  }
  
  .hero-text-nosotros > div {
    border: 1px solid #f49231;
    width: 58%;
    padding: 10px 25px;
    background-color: rgba(0, 0, 0, 0.4);
  }

  .hero-text-nosotros > div > h2{
    color: #f49231;
    font-family: 'Raleway', sans-serif;
  }
  
  .hero-text-nosotros > .line > p {
    font-size: 18px;
    color: white;
    margin-top: 20px;
    font-weight: 300;
    font-family: 'Raleway', sans-serif;
    line-height: 30px;
  }

  .hero-text-nosotros > .line > p > span{
    color: #f49231;
  }
  
  .hero-btn {
    background-color: #f49231;
    border: 0;
    height: 50px;
    width: 280px;
    color: white;
    margin-top: 20px;
    font-size: 18px;
    border-radius: 40px;
    transition: all 0.2s ease-in-out;
    box-shadow: 0 6px 8px 0 rgba(0, 0, 0, 0.2);
    letter-spacing: 1px;
    cursor: pointer;
  }
  
  .hero-btn:hover {
    transform: scale(1.1);
  }