.icon-card-small{
    display: flex;
    flex-direction: column;
    justify-content: flex-start !important;
    align-items: center;
    width: 200px;
    height: 190px;
    background-color: #f6f6f6;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
    padding: 20px 10px 0px 10px;
    border-radius: 10px;
  }
  
  .icon-card-small .icon-cont{
      min-height: 110px;
      display: flex;
      align-items: center;
      margin-top: 0;
  }

  .icon-card-small .icon-cont > img {
    width: 90px;
  }


  .icon-card-small .icon-card > p {
      color: #2d2d2d;

  }