.icon-section {
    display: flex;
    flex-direction: column;
    justify-content: center;
    text-align: center;
    align-items: center;
    height: 70vh;
    margin-top: 20px;
    margin-bottom: 80px;
  }
  
  .icon-section > h2 {
    font-size: 45px;
    color: #043E54;
  }
  
  .icon-container {
    display: grid;
    width: 84%;
    grid-template-columns: 1fr 1fr 1fr;
    grid-template-rows: 1fr;
    column-gap: 20px;
    row-gap: 20px;
  }