.hero-text {
    position: absolute;
    top: 25%;
    left: 11%;
    width: 35%;
  }
  
  .hero-text > h1 {
    font-size: 45px;
    color: white;
    margin-bottom: 20px;
    font-weight: bolder;
  }

  .hero-text > h1 > .orange{
    color: #f49231;

  }
  
  .hero-text > div {
    border-bottom: 1px solid white;
    width: 58%;
  }
  
  .hero-text > p {
    font-size: 25px;
    color: white;
    margin-top: 20px;
  }
  
  .cta-btn {
    background-color: #f49231;
    border: 0;
    height: 50px;
    width: 280px;
    color: white;
    margin-top: 20px;
    font-size: 18px;
    border-radius: 40px;
    transition: all 0.2s ease-in-out;
    box-shadow: 0 6px 8px 0 rgba(0, 0, 0, 0.2);
    letter-spacing: 1px;
    cursor: pointer;
  }
  
  .cta-btn:hover {
    transform: scale(1.1);
  }
  

  .inicia-btn {
    background-color: rgba(0, 0, 0, 0.2);
    border: 1px solid #f49231;
    height: 50px;
    width: 280px;
    color: white;
    margin-top: 20px;
    font-size: 18px;
    border-radius: 40px;
    transition: all 0.2s ease-in-out;
    box-shadow: 0 6px 8px 0 rgba(0, 0, 0, 0.2);
    letter-spacing: 1px;
    cursor: pointer;
  }
  
  .inicia-btn:hover {
    transform: scale(1.1);
  }