.icon-card {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    width: 302px;
    height: 225px;
    background-color: white;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
    
    border-radius: 10px;
    padding: 20px;
  }

  .icon-cont{
    height: 300px;
    margin-top: 15px;
  }
  
  .icon-card > img {
    width: 120px;
    margin-top: 15px;
  }

  .icon-card > p {
    color: #464646
  }