.form{
    background-image: url("../assets/Foto\ pag\ filtro\ blanca.png");
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    background-color: white;
      
    height: 60vh;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    padding-left: 150px;
  
  }
  
  
  
  
  .form > p{
    color: #043E54;
    font-size: 45px;
    font-weight: 900;
  }
  
  form{
    display: flex;
    flex-direction: column;
    margin-left: 10%;
    width: 400px;
  }
  
  form > input{
    margin: 10px 0;
    padding: 6px;
    border: none;
    border-radius: 5px;
    height: 25px;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
    background-color: rgba(255, 255, 255, 0.5);
  }
  
  form > input::placeholder{
    font-family: "Montserrat", sans-serif;
    font-size: 13px;
    font-style: italic;
    
  }
  
  form > textarea{
    padding: 6px;
    border: none;
    border-radius: 5px;
    resize: none;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
    background-color: rgba(255, 255, 255, 0.5);
  }
  
  form > textarea::placeholder{
    font-family: "Montserrat", sans-serif;
    font-size: 13px;
    font-style: italic;
  }
  
  form > #send{
    background-color: #043E54;
    border: 0;
    height: 45px;
    width: 230px;
    color: #f49231;
    margin-top: 20px;
    font-size: 17px;
    border-radius: 40px;
    transition: all 0.2s ease-in-out;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
    letter-spacing: 1px;
    cursor: pointer;
    align-self: center;
  }
  