.price-list{
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 90vh;
  }
  
  .price-cont{
    display: flex;
    flex-direction: column;
    justify-items: center;
    align-items: center;
    border: 2px solid #043E54;
    border-radius: 50px;
    padding: 50px 40px;
    margin: 100px;
  }
  
  .price-title{
    font-size: 40px;
    color: #043E54;
    margin: 0px;
    font-weight: 900;
    letter-spacing: 0.2em;
  }
  
  .price{
    font-size: 90px;
    color: #043E54;
    font-weight: bolder;
    margin: 0;
  }
  
  .price-sub{
    color: #f49231;
    font-size: 25px;
    margin: 0;  
    font-weight: bold;
  
  }
  
  .price-item{
    display: flex;
    margin: 10px;
    align-items: center;
    
  }
  
  .price-item-cont{
    margin: 20px;
  }
  
  .price-item > p{
    margin: 0px 2px;
    color: #043E54;
    font-size: 20px;
    
  } 
  
  .price-item > i{
    margin-right: 10px;
    color: #f49231;
    font-size: 20px;
    font-weight: bold;
  
  }
  
  .price-btn{
    background-color: #f49231;
    border: 0;
    height: 50px;
    width: 280px;
    color: white;
    margin-top: 10px;
    font-size: 18px;
    border-radius: 40px;
    transition: all 0.2s ease-in-out;
    box-shadow: 0 6px 8px 0 rgba(0, 0, 0, 0.2);
    letter-spacing: 1px;
    cursor: pointer;
  }
  
  .price-btn:hover {
    transform: scale(1.1);
  }