.book-page{
    display: grid;
    grid-auto-columns: 1fr;
    grid-auto-rows: 1/2fr;
    margin-top: 30px;
    background-color: rgba(0,62,87,.7);
    font-family: 'Raleway', sans-serif;
    grid-template-areas: 
        "cover-cont cover-cont player-cont player-cont player-cont"
        "cover-cont cover-cont text-cont text-cont suscribete"
        "cover-cont cover-cont text-cont text-cont suscribete"
        "cover-cont cover-cont text-cont text-cont suscribete"
}

.cover-cont{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 25px;
    
    grid-area: cover-cont;
}

.cover-cont > img{
    width: 80%;
}

.cover-cont > .i{
    color: #fff;
    font-size: 1rem;
}

.cover-cont > p{
    font-size: 1rem;
    color: #fff;
}


.text-cont {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    
    
    grid-area: text-cont;
}

.text-cont > .title {
    font-size: 2.5em;
    color: #fff;
    margin: 0.5rem;
    text-transform: uppercase;
}

.text-cont > .author{
    font-size: 1.7rem;
    color: #f49231;
    margin: 0.5rem;
}

.text-cont > .narrador{
    font-size: 1.5rem;
    margin-top: 0;
    color: #fff;
}

.text-cont > .sinopsis{
    font-size: 1rem;
    color: #fff;
    margin-top: 0.1rem;
}



.suscribete{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
    padding-top: 10%;
    
    
    grid-area: suscribete;
}

.suscribete > .link > .btn{
    background-color: #f49231;
    border: 0;
    height: 50px;
    width: 200px;
    color: white;
    margin-top: 20px;
    font-size: 15px;
    border-radius: 40px;
    transition: all 0.2s ease-in-out;
    box-shadow: 0 6px 8px 0 rgba(0, 0, 0, 0.2);
    letter-spacing: 1px;
    cursor: pointer;
    margin-bottom: -25px;
    
}

.suscribete > .link {
    z-index: 2; 
    margin-bottom: 0px;
}


.suscribete > .texto-suscribete{
    font-size: 14px;
    color: #fff;
    text-align: center;
    background-color: rgba(1,1,1,.5);
    width: 50%;
    padding: 10%;
    margin-top: 5px;
    z-index: 0;
}

.player-cont{
    grid-area: player-cont;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
    max-height: 120px;
    padding-top: 20px;
}

.player-cont > .gratis{
    font-weight: 700;   
    font-size: 1.2rem;
    color: #fff;
    margin: 0;
}